.preloader {
  position: relative;
}

.preloader:before, .preloader:after {
  content: '';
  display: block;
  width: 30px;
  height: 30px;
  position: absolute;
  z-index: -1;
  background-color: #f0d2b0;
  border-radius: 50%;
  opacity: 0;
  transition: transform .8s ease, z-index 0s linear .8s, opacity .2s ease .6s;
}

@media all and (min-width: 48em) {
  .preloader:before, .preloader:after {
    width: 50px;
    height: 50px;
  }
}

.preloader:before {
  top: 5%;
  left: 5%;
}

@media all and (min-width: 48em) {
  .preloader:before {
    top: 10%;
    left: 10%;
  }
}

.preloader:after {
  bottom: 5%;
  right: 5%;
}

@media all and (min-width: 48em) {
  .preloader:after {
    bottom: 10%;
    right: 10%;
  }
}

.preloader_active {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.preloader_active:before, .preloader_active:after {
  z-index: 11;
  opacity: 1;
  transform: scale(40);
  transition: transform .8s ease-out, z-index 0s linear, opacity .2s ease;
}

@media all and (min-width: 48em) {
  .preloader_active:before, .preloader_active:after {
    transform: scale(50);
  }
}

.preloader_active .preloader__spinner {
  z-index: 12;
}

.preloader_active .preloader__ball {
  animation-name: spin-balls;
}

.preloader__spinner {
  width: 180px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
}

.preloader__ball {
  display: inline-block;
  float: left;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 10px;
  background-color: #797979;
  animation-iteration-count: infinite;
  animation-fill-mode: both;
}

.preloader__ball:nth-child(1) {
  animation-delay: 0.76s;
  animation-duration: 0.72s;
}

.preloader__ball:nth-child(2) {
  animation-delay: -0.12s;
  animation-duration: 1.44s;
}

.preloader__ball:nth-child(3) {
  animation-delay: 0.03s;
  animation-duration: 0.95s;
}

.preloader__ball:nth-child(4) {
  animation-delay: 0.67s;
  animation-duration: 0.7s;
}

.preloader__ball:nth-child(5) {
  animation-delay: 0.68s;
  animation-duration: 1.1s;
}

.preloader__ball:nth-child(6) {
  animation-delay: -0.18s;
  animation-duration: 1.32s;
}

.preloader__ball:nth-child(7) {
  animation-delay: 0.71s;
  animation-duration: 1.11s;
}

.preloader__ball:nth-child(8) {
  animation-delay: 0.34s;
  animation-duration: 1.55s;
}

.preloader__ball:nth-child(9) {
  animation-delay: 0.32s;
  animation-duration: 1.28s;
}

a,
button,
[type="submit"] {
  touch-action: manipulation;
}

picture {
  display: block;
}

picture > img {
  width: 100%;
}

img,
picture,
svg {
