.header__title {
  position: relative;
}

@keyframes glitch-title-anim-1 {
  0% {
    clip: rect(51px, 540px, 64px, 0);
  }
  10% {
    clip: rect(4px, 540px, 14px, 0);
  }
  20% {
    clip: rect(63px, 540px, 16px, 0);
  }
  30% {
    clip: rect(64px, 540px, 29px, 0);
  }
  40% {
    clip: rect(20px, 540px, 57px, 0);
  }
  50% {
    clip: rect(48px, 540px, 38px, 0);
  }
  60% {
    clip: rect(13px, 540px, 26px, 0);
  }
  70% {
    clip: rect(40px, 540px, 32px, 0);
  }
  80% {
    clip: rect(56px, 540px, 60px, 0);
  }
  90% {
    clip: rect(51px, 540px, 25px, 0);
  }
  100% {
    clip: rect(56px, 540px, 60px, 0);
  }
}

@keyframes glitch-title-anim-2 {
  0% {
    clip: rect(31px, 540px, 2px, 0);
  }
  10% {
    clip: rect(20px, 540px, 32px, 0);
  }
  20% {
    clip: rect(11px, 540px, 9px, 0);
  }
  30% {
    clip: rect(54px, 540px, 32px, 0);
  }
  40% {
    clip: rect(29px, 540px, 33px, 0);
  }
  50% {
    clip: rect(64px, 540px, 41px, 0);
  }
  60% {
    clip: rect(48px, 540px, 60px, 0);
  }
  70% {
    clip: rect(45px, 540px, 34px, 0);
  }
  80% {
    clip: rect(55px, 540px, 64px, 0);
  }
  90% {
    clip: rect(37px, 540px, 23px, 0);
  }
  100% {
    clip: rect(20px, 540px, 60px, 0);
  }
}

.header__title:before, .header__title:after {
  content: attr(data-text);
  position: absolute;
  top: 0;
