  .menu-toggle {
    top: 43px;
    left: 29px;
  }
}

.menu-toggle:before {
  content: '';
  display: block;
  width: 1px;
  height: 30px;
  position: absolute;
  top: 9px;
  right: -1px;
  background-color: #f0d2b0;
}

.menu-toggle_active {
  border-color: #797979;
}

.menu-toggle_active:before {
  width: 2px;
}

.menu-toggle_active .menu-toggle__line {
  width: 25px;
  height: 1px;
}

.menu-toggle_active .menu-toggle__line:nth-of-type(1) {
  transform: rotate(28deg) translateX(9px);
}

.menu-toggle_active .menu-toggle__line:nth-of-type(2) {
  transform: rotate(90deg) translateY(3px);
}

.menu-toggle_active .menu-toggle__line:nth-of-type(3) {
  transform: rotate(-28deg) translateX(9px);
}

.menu-toggle__line {
  display: block;
  width: 40px;
  height: 2px;
  position: absolute;
  left: calc(50% - 20px);
  background-color: #797979;
  transition: all .2s ease;
  will-change: transform;
}

.menu-toggle__line:nth-of-type(1) {
  top: 13px;
}

.menu-toggle__line:nth-of-type(2) {
  top: 23px;
}

.menu-toggle__line:nth-of-type(3) {
  top: 33px;
}

